.pageWrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 32px 16px;
  box-sizing: border-box;
}

.header {
  width: 100%;
  max-width: 1080px;
  margin-bottom: 24px;
}

.title {
  margin: 0;
  font-size: 28px;
  font-weight: 700;
  text-align: center;
}

.link {
  margin-left: 8px;
  color: #1976d2;
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}

.main {
  width: 100%;
  max-width: 1080px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}

.section {
  width: 100%;
}

.sectionTitle {
  margin: 0 0 16px;
  font-size: 20px;
  font-weight: 600;
}

.mediaGrid {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: center;
}

.image {
  width: 320px;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  background: #f2f2f2;
}

.video {
  width: 360px;
  height: 220px;
  border-radius: 8px;
  background: #000;
}


